body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Mada', sans-serif;
}


#recipe {
  width: 80vw;
  display: flex;
  flex-wrap: wrap;
  background-color: rgba(202, 202, 202, 0.8);
  padding: 5vh 3vh;
  margin:10vh;
}
#recipe img {
  margin-left: 1vw;
  width: 30vw;
  height: 30vw;
}
#ingrdient-table {
  margin-left: 3vw;
  width: 40vw;
}
#qty-column {
  margin-right: vw;
  width: 10vw;
}
#left-container {
  display: flex;
  flex-direction: column;
}
#howtodo{
  text-align: center;
}
#left-container a{
  color: grey;
  margin-top: 2vh;
  text-decoration: none;
}
#ingrdient-table hr{
  border: 0.5px solid rgb(173, 173, 173);
}
#left-vontainer{
  
}
#recipes{
  margin: 0;
  padding: 0;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#recipes h1 span{
  color: #ff9a9a;
}
#recipes-container{
  width: 80vw;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
#recipes-card{
  width: 25vw;
  height: 80vh;
  padding: 3vh 0;
  margin-bottom : 6vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(240, 240, 240);
  -webkit-transition: 1s;
  transition: 1s;
}
#recipes-card:hover {
  background-color: rgba(202, 202, 202, 0.8);
}

#recipes-card img{
width: 22vw;
}

#card-text{
width: 15vw;
}

#card-text hr{
  border: 0.5px solid rgb(214, 214, 214);
}


#search form {
  padding-top: 10vh;
  text-align: center;
  height: 60vh;
  background-image: url(/static/media/background-search.7f4932af.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

#input-search {
  margin: 0;
  padding: 0;
  border: none;
  height: 7vh;
  width: 20vw;
  border-right: 1px solid gainsboro;
}

#input-submit {
  color: rgb(148, 148, 148);
  margin: 0;
  padding: 0;
  border: none;
  height: 7vh;
  width: 7vw;
}
#box-1 {
  height: 60vh;
  display: flex;
  flex-wrap: wrap;
}
#img-1 {
  background-image: url(/static/media/box1bis.5aa4a5c0.jpg);
  background-size: cover;
  height: 60vh;
  width: 50vw;
}
#menu-1 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  width: 50vw;
}
#box-2 {
  height: 60vh;
  display: flex;
  flex-wrap: wrap;
}
#img-2 {
  background-image: url(/static/media/box2.5d3795ba.jpg);
  background-size: cover;
  height: 60vh;
  width: 50vw;
}
#menu-2 {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 60vh;
  width: 50vw;
}
#box-3 {
  height: 60vh;
  display: flex;
  flex-wrap: wrap;
}
#img-3 {
  background-image: url(/static/media/box3.5c1be42e.jpg);
  background-size: cover;
  height: 60vh;
  width: 50vw;
}
#menu-3 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 60vh;
  width: 50vw;
}
.menu-card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 23vw;
}
.menu-card p {
  text-align: justify;
}
.menu-card a {
  height: 7vh;
  width: 12vw;
  text-decoration: none;
  color: white;
  display: flex;
  background-color: rgb(219, 219, 219);
  justify-content: center;
  align-items: center;
}

#advancedSearch {
    display: flex;
    margin: 0 auto;
    width: 85vw;
}

#advancedSearchBox {
    width: 30vw; 
}

#advancedSearchForm {
    width: 60vw; 
    background-color: aqua;
    display: flex;
}
#health-labels {
  background-color: rgb(224, 224, 224);
}
#health-labels h2 {
  color:rgb(97, 97, 97);
  width: 25vw;
  margin: 0 2.5vh;
}
#health-labels label {
  display: flex;
  align-items: center;
  width: 10vw;
  height: 4vh;
  margin: 1vh;
  background-color: rgb(238, 238, 238);
  color: rgb(97, 97, 97);
  -webkit-transition: 1s;
  transition: 1s;
}
#health-labels label:hover {
  color: black;
  background-color: rgb(194, 194, 194);
}
#diets {
  width: 25vw;
  display: flex;
  flex-wrap: wrap;
  padding: 3vh;
  justify-content: space-around;
}
#allergies {
  width: 25vw;
  display: flex;
  flex-wrap: wrap;
  padding: 3vh;
  justify-content: space-around;
}
#line-health {
  
  width: 20vw;
  margin: 0 auto;
  padding: 0;
  border: 0.5px solid rgb(179, 179, 179);
}

#navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 3vw;
  background-color: white;
  height: 8.9vh;
}
#navbar ul {
  display: flex;
  list-style: none;
  align-items: center;
  margin: 0;
  padding: 0;
}
#navbar ul li {
  padding-left: 1.2vh;
}
#navbar h1 {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Rock Salt", cursive;
  font-size: 4vh;
}
#navbar a {
  text-decoration: none;
  color: #272727;
}

#label-composition {
  display: flex;
  justify-content: center;
  align-items: center;
}

#userbar {
    background-color: #272727;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
#userbar ul{
    display: flex;
    list-style: none;
    justify-content: flex-end;
    align-items: center;
    height: 6vh;
    margin: 0;
    padding: 0;
    padding-right: 2vw;
}
#userbar ul li{
    padding-right: 1vw;
    color: #d3d3d3;

}
#imgProfile {
    height: 4vh;
    border-radius: 2vh
}
#off-img{
    height: 3vh
}
#userbar ul li a {
    color: #62b3ff;
    text-decoration: none;
}

#table-menu {
    display: flex;
}
#profile{
  display: flex;
  justify-content: space-around;
  padding-top: 4vh;
}
#profile-container{
  display: flex;
  justify-content: center;
  align-items: center;
}
#user-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 25vw;
  background-color: rgb(224, 224, 224);
}
#user-form{
  display: flex;
  justify-content: center;
  align-items: center;
}
#user-container h3{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25vw;

}
